import { setLocalStorage } from '@/assets/js/localStorage'

const mutations = {
  SET_DETAIL_TAB_CUR: (state, index) => {
    state.detailTabCur = index
    setLocalStorage('detailTabCur', index)
  },
  SET_ORDER_TAB_CUR: (state, value) => {
    state.orderTabCur = value
    setLocalStorage('orderTabCur', value)
  },
  SET_ORDER_TAB_CUR_STATE: (state, value) => {
    state.orderTabCurState = value
    setLocalStorage('orderTabCurState', value)
  },
  SET_GUARANTY_TAB_CUR: (state, value) => {
    state.guarantyTabCur = value
    setLocalStorage('guarantyTabCur', value)
  },
  SET_GUARANTY_TAB_CUR_STATE: (state, value) => {
    state.guarantyTabCurState = value
    setLocalStorage('guarantyTabCurState', value)
  },
  SET_CLAIM_TAB_CUR: (state, value) => {
    state.claimTabCur = value
    setLocalStorage('claimTabCur', value)
  },
  SET_CLAIM_TAB_CUR_STATE: (state, value) => {
    state.claimTabCurState = value
    setLocalStorage('claimTabCurState', value)
  },
	setUserInfo(state, value){
		state.userInfo = value
		setLocalStorage('userInfo', value)
	},
	setToken(state, value){
		state.token = value
		setLocalStorage('token', value)
	}
}

export default mutations
