const productList = resolve => require(['@/components/product/TheProductList.vue'], resolve)
const applicant = resolve => require(['@/components/product/TheDetailApplicant.vue'], resolve)
const orderComfirm = resolve => require(['@/components/product/TheDetailOrderComfirm.vue'], resolve)
const wechat = resolve => require(['@/components/order/wechatPay.vue'], resolve)
const customerNotification = resolve => require(['@/components/product/TheCustomerNotification.vue'], resolve)
