import {
	axiosGet,
	axiosPost
} from '../request'

// 资料获取
export const getRecord = (params) =>
	axiosGet({
		config: {
			url: '/member/claim/getRecord',
			params
		}
	})

//资料保存
export const upload = (params) =>
	axiosPost({
		config: {
			url: '/common/file/upload',
			params
		}
	})

//资料保存
export const submit = (params) =>
	axiosGet({
		config: {
			url: '/member/claim/submit',
			params
		}
	})
