export default Vue => {
  Vue.filter('claimStatus', function (value) {
    let res = ''
    switch (parseInt(value)) {
      case 0 : res = '待提交'; break
      case 1 : res = '待审核'; break
      case 2 : res = '已理算'; break
      case 3 : res = '已理赔'; break
      case 4 : res = '待修改'; break
      case 5 : res = '已撤销'; break
      case 6 : res = '拒赔'; break
      default: console.log('not defined status: ' + value)
    };
    return res
  })
  Vue.filter('recordStatus', function (value) {
    let res = ''
    switch (value) {
      case 'upload' : res = '待提交'; break
      case 'uploaded' : res = '待提交'; break
      case 'check' : res = '待审核'; break
      case 'unpassed' : res = '未通过'; break
      case 'passed' : res = '已通过'; break
      default: console.log('not defined record-status: ' + value)
    };
    return res
  })
}
