import Vue from 'vue'
import store from '../../store/index'
/*import VConsole from 'vconsole/dist/vconsole.min.js'*/
// let vConsole = new VConsole()
// console.log('VConsole is cool')
// 判断是否微信浏览器
export function isWecht (func, delay) {
  const ua = navigator.userAgent.toLowerCase()
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
    return true
  } else {
    return false
  }
}

export function weChatAuthorize () {
  if (isWecht()) {
    Vue.prototype.$api.user.weChatAuthorize().then(res => {
      if (res.code === 1) {
        store.commit('SET_TOKEN', res.data.csrfToken)
        store.commit('SET_USER_INFO', res.data.member)
      //   //  SET_TOKEN(res.data.csrfToken)
      //   // this.SET_USER_INFO(res.data.member)
      } else {
        store.commit('SET_TOKEN', '')
        store.commit('SET_USER_INFO', '')
      }
    })
  }
}
